import React, { useState, useEffect } from "react";
import {
  ModalRoot,
  ModalCard,
  FormLayout,
  Input,
  Avatar,
  InfoRow,
  FormLayoutGroup,
  Div,
} from "@vkontakte/vkui";

const ModalInfoVIP = ({ setActiveModal, openPaymentVIP, type }) => {
  const [choose, setChoose] = useState(2);

  const targetId = (e) => {
    setChoose(+e.currentTarget.id);
    console.log("targetId choose", choose);
    console.log("targetId ", +e.currentTarget.id);
  };

  return (
    <>
      {
        (type === "VIP" ? (
          <ModalRoot activeModal="modal-info-vip">
            <ModalCard
              id="modal-info-vip"
              onClose={() => setActiveModal(null)}
              header="Что дает VIP аккаунт"
              caption={
                <>
                  <Div
                    style={{
                      display: "flex",
                      alignItems: "start",
                      justifyContent: "center",
                      flexDirection: "column",
                    }}
                  >
                    <InfoRow>
                      1. Продажа DurkaCoins ниже рыночной стоимости на 80%{" "}
                    </InfoRow>
                    <InfoRow>2. Цветной никнейм в ТОП-е, играх</InfoRow>
                    <InfoRow>3. Gold пилюля - 10 шт </InfoRow>
                    <InfoRow>4. Одиночная камера - 100 шт </InfoRow>
                    <InfoRow>5. Эксклюзивные стикеры НАРУТО</InfoRow>
                    <InfoRow>5. Стикеры ДУРКА</InfoRow>
                    <InfoRow style={{ marginBottom: "10px" }}>
                      Выберите цвет:{" "}
                    </InfoRow>
                    <Div
                      style={{
                        display: "flex",
                        alignItems: "center",
                        justifyContent: "space-around",
                        flexDirection: "row",
                        width: "100%",
                      }}
                    >
                      <Div
                        id="1"
                        // onClick={(e) => {
                        //   setChoose(+e.currentTarget.id);
                        //   console.log("choose 1", choose);
                        //   console.log("+e.currentTarget.id 1", +e.currentTarget.id);
                        // }}
                        onClick={targetId}
                        style={{
                          borderRadius: "20px",
                          height: "30px",
                          width: "30px",
                          backgroundColor: "green",
                          border: choose === 1 ? "3px solid #FFB800" : "",
                        }}
                      ></Div>
                      <Div
                        id="2"
                        onClick={targetId}
                        style={{
                          borderRadius: "20px",
                          height: "30px",
                          width: "30px",
                          backgroundColor: "#D40000",
                          border: choose === 2 ? "3px solid #FFB800" : "",
                        }}
                      ></Div>
                      <Div
                        id="3"
                        onClick={targetId}
                        style={{
                          borderRadius: "20px",
                          height: "30px",
                          width: "30px",
                          backgroundColor: "blue",
                          border: choose === 3 ? "3px solid #FFB800" : "",
                        }}
                      ></Div>
                      <Div
                        id="4"
                        onClick={targetId}
                        style={{
                          borderRadius: "20px",
                          height: "30px",
                          width: "30px",
                          backgroundColor: "#800080",
                          border: choose === 4 ? "3px solid #FFB800" : "",
                        }}
                      ></Div>
                    </Div>
                  </Div>
                </>
              }
              actions={[
                {
                  title: "Купить VIP",
                  mode: "commerce",
                  action: () => {
                    openPaymentVIP(choose, "vip");
                  },
                },
              ]}
            ></ModalCard>
          </ModalRoot>
        ) : (
          <ModalRoot activeModal="modal-info-gold-vip">
            <ModalCard
              id="modal-info-gold-vip"
              onClose={() => setActiveModal(null)}
              header="Что дает GOLD VIP аккаунт"
              caption={
                <>
                  <Div
                    style={{
                      display: "flex",
                      alignItems: "start",
                      justifyContent: "center",
                      flexDirection: "column",
                    }}
                  >
                    <InfoRow>
                      1. Продажа DurkaCoins ниже рыночной стоимости на 90%{" "}
                    </InfoRow>
                    <InfoRow>2. Добавление в контакты группы</InfoRow>
                    <InfoRow>3. Цветной никнейм в ТОП-е, играх</InfoRow>
                    <InfoRow>4. Gold пилюля - 100 шт </InfoRow>
                    <InfoRow>5. Одиночная камера - 1000 шт </InfoRow>
                    <InfoRow>6. Отпуск - 1000 шт </InfoRow>
                    <InfoRow>7. Все стикеры из магазина ДУРКИ</InfoRow>
                    <InfoRow style={{ marginBottom: "10px" }}>
                      Выберите цвет:{" "}
                    </InfoRow>
                    <Div
                      style={{
                        display: "flex",
                        alignItems: "center",
                        justifyContent: "space-around",
                        flexDirection: "row",
                        width: "100%",
                      }}
                    >
                      <Div
                        id="1"
                        // onClick={(e) => {
                        //   setChoose(+e.currentTarget.id);
                        //   console.log("choose 1", choose);
                        //   console.log("+e.currentTarget.id 1", +e.currentTarget.id);
                        // }}
                        onClick={targetId}
                        style={{
                          borderRadius: "20px",
                          height: "30px",
                          width: "30px",
                          backgroundColor: "green",
                          border: choose === 1 ? "3px solid #FFB800" : "",
                        }}
                      ></Div>
                      <Div
                        id="2"
                        onClick={targetId}
                        style={{
                          borderRadius: "20px",
                          height: "30px",
                          width: "30px",
                          backgroundColor: "#D40000",
                          border: choose === 2 ? "3px solid #FFB800" : "",
                        }}
                      ></Div>
                      <Div
                        id="3"
                        onClick={targetId}
                        style={{
                          borderRadius: "20px",
                          height: "30px",
                          width: "30px",
                          backgroundColor: "blue",
                          border: choose === 3 ? "3px solid #FFB800" : "",
                        }}
                      ></Div>
                      <Div
                        id="4"
                        onClick={targetId}
                        style={{
                          borderRadius: "20px",
                          height: "30px",
                          width: "30px",
                          backgroundColor: "#800080",
                          border: choose === 4 ? "3px solid #FFB800" : "",
                        }}
                      ></Div>
                    </Div>
                  </Div>
                </>
              }
              actions={[
                {
                  title: "Купить GOLD VIP",
                  mode: "commerce",
                  action: () => {
                    openPaymentVIP(choose, "gold");
                  },
                },
              ]}
            ></ModalCard>
          </ModalRoot>
        ))
      }
    </>
  );
};

export { ModalInfoVIP };
