import React from "react";
import {
  ModalRoot,
  ModalCard,
  FormLayout,
  Input,
  Avatar,
  InfoRow,
  FormLayoutGroup,
} from "@vkontakte/vkui";

const ModalBuyTransfer = ({
  setActiveModal,
  openPaymentSystem,
  onChangeHandlerPayment,
  onChangeHandlerAmount,
  type,
  onChangeHandlerId,
  transfer,
  text,
  exchangeQiwiPay,
  onChangeQiwiAccount,
  withdraw,
  comission
}) => {



  return (
    <>
      {type === "buy" ? (
        <ModalRoot activeModal="buy-porncoins">
          <ModalCard
            id="buy-porncoins"
            onClose={() => setActiveModal(null)}
            icon={
              <Avatar
                size={72}
                src="https://sun9-18.userapi.com/c850608/v850608982/19c3da/KXFMppBTpBo.jpg"
              />
            }
            header="Купить PornCoins"
            caption={
              <>
                <FormLayout>
                  <InfoRow style={{ color: "red" }}>{text || ""}</InfoRow>
                  <Input
                    top="Укажите кол-во PornCoins"
                    placeholder="5"
                    onChange={onChangeHandlerPayment}
                    type="number"
                  />
                </FormLayout>
                <InfoRow id="toPay">К оплате: 0 руб</InfoRow>
              </>
            }
            actions={[
              {
                title: "Купить",
                mode: "commerce",
                onChange: { onChangeHandlerAmount },

                action: () => {
                  openPaymentSystem();
                },
              },
            ]}
          ></ModalCard>
        </ModalRoot>
      ) : type === "transfer" ? (
        <ModalRoot activeModal="transfer">
          <ModalCard
            id="transfer"
            onClose={() => setActiveModal(null)}
            icon={
              <Avatar
                size={72}
                src="https://sun9-18.userapi.com/c850608/v850608982/19c3da/KXFMppBTpBo.jpg"
              />
            }
            header="Перевести PornCoins"
            caption={
              <>
                <FormLayout>
                  <Input
                    top="Введите ссылку на получателя"
                    placeholder="https://vk.com/porncoin"
                    onChange={onChangeHandlerId}
                    type="text"
                  />
                  <FormLayoutGroup top="Укажите кол-во PornCoins">
                    <Input
                      onChange={onChangeHandlerAmount}
                      placeholder="10"
                      type="number"
                    />
                  </FormLayoutGroup>
                </FormLayout>
              </>
            }
            actions={[
              {
                title: "Перевести",
                mode: "commerce",
                action: () => {
                  transfer();
                },
              },
            ]}
          ></ModalCard>
        </ModalRoot>
      ) : type === "replenishment" ? (
        <ModalRoot activeModal="replenishment">
          <ModalCard
            id="replenishment"
            onClose={() => setActiveModal(null)}
            icon={
              <Avatar
                size={72}
                src="https://sun9-18.userapi.com/c850608/v850608982/19c3da/KXFMppBTpBo.jpg"
              />
            }
            header="Пополнение баланса"
            caption={
              <>
                <FormLayout>
                  <Input
                    top="Введите сумму в рублях"
                    placeholder="1"
                    onChange={onChangeHandlerAmount}
                    type="number"
                  />
                </FormLayout>
              </>
            }
            actions={[
              {
                title: "Пополнить",
                mode: "commerce",
                action: () => {
                  exchangeQiwiPay();
                },
              },
            ]}
          ></ModalCard>
        </ModalRoot>
      ) : (
        <ModalRoot activeModal="withdraw">
          <ModalCard
            id="withdraw"
            onClose={() => setActiveModal(null)}
            icon={
              <Avatar
                size={72}
                src="https://sun9-18.userapi.com/c850608/v850608982/19c3da/KXFMppBTpBo.jpg"
              />
            }
            header="Вывод средств"
            caption={
              <>
                <FormLayout>
                    <Input
                      top="Введите номер QIWI Кошелька"
                      placeholder="+7xxxxxxxxxx"
                      onChange={onChangeQiwiAccount}
                      type="text"
                    />
                    <Input
                      top="Введите сумму в рублях"
                      placeholder="10"
                      onChange={onChangeHandlerAmount}
                      type="number"
                    />
                    <InfoRow id="comission">Комиссия PornCoin: 10%</InfoRow>
                </FormLayout>
              </>
            }
            actions={[
              {
                title: "Вывести",
                mode: "commerce",
                action: () => {
                  withdraw();
                },
              },
            ]}
          ></ModalCard>
        </ModalRoot>
      )}
    </>
  );
};

export { ModalBuyTransfer };
