import React from "react";
import {
  Panel,
  PanelHeaderSimple,
  PanelHeaderBack,
  Group,
  Header,
  Cell,
  Div,
  Link,
  InfoRow,
  Input,
  FormLayoutGroup,
  FormLayout,
  Button,
} from "@vkontakte/vkui";

const ExchangeSell = ({
  go,
  id,
  pornCoins,
  course,
  addExchangeOffer,
  onChangeAction,
  costOnePC,
  inputPornCoins,
  qiwiAccount,
  openPaymentVIP,
  openInfoVIP,
}) => {
  return (
    <Div
      style={{
        margin: 10,
      }}
    >
      <Group>
        <Cell>
          <InfoRow header="Ваш баланс">
            <Div className="componentsExchangeSellInfoRow">
              <Div className="componentsExchangeSellBalance">{pornCoins}</Div>
              <Div className="iconPornCoin16px" style={{ marginLeft: 4 }} />
            </Div>
          </InfoRow>

          <InfoRow header="Текущий курс">
            <Div className="componentsExchangeSellInfoRow">
              <Div className="iconPornCoin16px" style={{ marginRight: 4 }} />
              <Div className="componentsExchangeSellBalance">
                {" "}
                = {course}/₽{" "}
              </Div>
            </Div>
          </InfoRow>

          <Div
            style={{
              marginBottom: 5,
              marginTop: 5,
            }}
          >
            <Div
              style={{
                marginBottom: 5,
                marginTop: 5,
              }}
            >
              Введите кол-во PornCoins
            </Div>
            <Input
              value={inputPornCoins}
              onChange={(e) => onChangeAction(e, "PC")}
              type="text"
            ></Input>

            <Div
              style={{
                marginBottom: 5,
                marginTop: 5,
              }}
            >
              Введите цену за 1 PornCoin в руб
            </Div>
            <Input
              value={costOnePC}
              onChange={(e) => onChangeAction(e, "costOnePC")}
              type="text"
            ></Input>
          </Div>

          <Div
            style={{
              marginBottom: 5,
              marginTop: 10,
            }}
          >
            <Group>
              <Button
                style={{ backgroundColor: "green", color: "white" }}
                onClick={addExchangeOffer}
                mode="commerce"
                size="xl"
                stretched
              >
                Добавить предложение
              </Button>
            </Group>

            <Div className="componentsExchangeSellBuyVIP">
              Купить VIP аккаунт
            </Div>
            <Div className="componentsExchangeSellBuyVIP">
              <Button
                style={{
                  backgroundColor: "#FFB800",
                  color: "white",
                  marginRight: "5px",
                }}
                onClick={() => openInfoVIP("goldVIP")}
                mode="commerce"
                size="xl"
                stretched
              >
                GOLD VIP
              </Button>
              <Button
                style={{
                  backgroundColor: "#800080",
                  color: "white",
                  marginLeft: "5px",
                }}
                onClick={() => openInfoVIP("VIP")}
                mode="commerce"
                size="xl"
                stretched
              >
                VIP
              </Button>
            </Div>

            <Group>
              <Div className="componentsExchangeSellFooter">
                Предложение размещается на 24 часа
              </Div>
              <Div className="componentsExchangeSellFooter">
                Чем ниже стоимость за 1 PornCoin, тем выше предложение в списке
              </Div>
            </Group>
          </Div>
        </Cell>
      </Group>
    </Div>
  );
};

export { ExchangeSell };
