import React from "react";
import {
  ModalRoot,
  ModalCard,
  InfoRow,
  FormLayout,
  Input,
  Div,
  Avatar
} from "@vkontakte/vkui";

// import { formatCoins } from "../helpers";

const ModalSharing = ({
  setActiveModal,
  openPopoutSharing
}) => {
  return (
    <ModalRoot activeModal="modal-sharing">
      <ModalCard
        id="modal-sharing"
        onClose={() => setActiveModal(null)}
        icon={
          <Avatar
            size={72}
            src="https://sun9-18.userapi.com/c850608/v850608982/19c3da/KXFMppBTpBo.jpg"
          />
        }
        header="Продай быстрее!"
        caption={
          <>
            <Div
              style={{
                display: "flex",
                alignItems: "start",
                justifyContent: "center",
                flexDirection: "column",
              }}
            >
              <Div>
               Ваше предложение успешно добавлено!
               Чтобы ваше предложение купили быстрее, Вы можете поделиться
                ссылкой с друзьями.
              </Div>
            </Div>
          </>
        }
        actions={[
          {
            title: "Рассказать друзьям",
            mode: "commerce",
            action: () => {
              openPopoutSharing("exchange");
            },
          },
        ]}
      ></ModalCard>
    </ModalRoot>
  );
};

export { ModalSharing };
